/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 15px;
    width: 58px;
    height: 58px;
    margin: 0;
    padding: 0;
    color: #1f1f24;
    font-size: 13px;
    line-height: 52px;
    letter-spacing: 0.02em;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    text-transform: capitalize;
    @include mkdfTransform(translateZ(0));
    background-color: #fff;
    border-radius: 10px;
    border-width: 2px;
    border-style: solid;
    text-indent: 1px;
}

@mixin mkdfWooCommerceSaleStyle() {
    left: 15px;
    border-color: #08104d;
    background-color: #08104d;
    color: #fff;
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    left: 15px;
    border-color: #d7e3f7;
}

@mixin mkdfWooCommerceNewProductStyle() {
    left: 15px;
    border-color: $first-main-color;
    background-color: $first-main-color;
    color: #fff;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 10px 0 0;
    line-height: 1;
    color: $default-text-color;
    
    * {
        text-transform: uppercase;
        font-size: 13px;
        letter-spacing: .02em;
        line-height: inherit;
    }
    
    del {
        margin: 0 1px 0 0;
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1;
    letter-spacing: 2px;
    color: $first-main-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */