/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 100;
					$space_between_items: 20;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 48%;
		    margin: 0;
		    padding: 0 20px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 52%;
        padding: 0 0 0 29px;
        float: left;
        box-sizing: border-box;
	
	    .mkdf-single-product-title {
		    display: block;
		    margin: 0 0 10px;
	    }
	
	    .price {
		    @include mkdfWooCommercePriceStyle();
			clear: both;
			margin-top: 11px;

			*{
				font-size: 18px;
				letter-spacing: .05em;
			}
	    }
	
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1em;
		    margin: 13px 0 3px;
	    }

		.woocommerce-review-link {
			display: none;
		}
	
	    .woocommerce-product-details__short-description {
		    @include mkdfRelativeHolderLayout();
		    margin: 30px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 16px 0 0;

			span,
			a {
				text-transform: capitalize;
				font-size: 13px;
				letter-spacing: .02em;
			}
		
		    > span {
			    display: block;
			    margin: 4px 0;
				color: $default-text-color;

				>span,
				>a {
					color: $default-text-color;
				}
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include mkdfRelativeHolderLayout();
		    margin:  35px 0 0;

			button.single_add_to_cart_button {
                padding-right: 60px;
				background-color: $first-main-color;

				&:after {
					content: "";
                    position: absolute;
                    display: block;
                    right: 35px;
                    top: calc(50% - 10px);
					width: 15px;
					height: 19px;
					background: url('../img/shopping-bag.svg');
				}

				&:hover {
					background-color: $default-heading-color;
				}
			}
	    }
	
	    .mkdf-quantity-buttons {
		    margin: 0 20px 0 0;
	    }

		.mkdf-single-product-meta-title {
			margin-top: 32px;

			h6 {
				margin: 0;
			}
		}
    }
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 71px 0 0;
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				
				a {
					position: relative;
					vertical-align: top;
					color: $default-heading-color;
					display: inline-block;
					font-family: $default-text-font;
					font-size: 16px;
					font-weight: 600;
					padding: 0 20px 16px 0;
					margin: 0 20px 0 0;

					&:after {
						content: '';
						width: 100%;
						height: 2px;
						position: absolute;
						left: 0;
						bottom: 0;
						background-color: $first-main-color;
						opacity: 0;
						transition: opacity .2s ease;
					}

					@include ipad-landscape {
						margin-bottom: 25px;
					}
				}

				&.active,
				&:hover {
					a {
						&:after {
							opacity: 1;
							transition: opacity .2s ease;
						}
					}
				}
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 21px 0 20px;
			}
		}
		
		table {
			margin: 9px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				font-size: 13px;
				letter-spacing: .02em;
				font-weight: 500;
				font-family: $default-text-font;
				color: $default-text-color;
				
				p {
					margin: 0;
					font-weight: 500;
				}
			}
			
			th {
				width: 1%;
				padding: 0 10px 0 0;
                text-transform: capitalize;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			margin: 5px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 5px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 15px;
				}
				
				p {
					margin: 0;

					&.comment-form-author,
					&.comment-form-email {
						width: 48%;
						float: left;

						#author,
						#email {
							width: 100%;
						}

						label {
							float: left;
							width: 100%;
						}
					}

					&.comment-form-author {
						margin-right: 2.2%;
					}

					&.form-submit {
						.submit {
							margin-top: 10px;
						}
					}
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}

					&.active:after {
						@supports (-ms-ime-align: auto) {
							width: 95px;
						}

						@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
							width: 95px;
						}
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 57px 0 0;
		
		> h2 {
			display: block;
			margin: 33px 0 31px;
			@include mkdfH4();
		}
		
		.product {
			
			.mkdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */