/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .mkdf-cart-number {
            background-color: $first-main-color;
            color: #fff;
            border-radius: 100%;
            position: relative;
            right: 5px;
            top: -5px;
            font-size: 10px;
            width: 16px;
            height: 16px;
            text-align: center;
            line-height: 17px;
            display: inline-block;
        }

        .mkdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        &.mkdf-header-cart-icon-pack {
            
            .mkdf-cart-icon {
                font-size: 20px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            
            .mkdf-cart-icon {

                > * {
                    vertical-align: middle;
                }

                .st0 {
                    fill: #08104d;
                    @include mkdfTransition(fill .5s ease);
                }

                .st1{
                    fill: #D7E3F7;
                    @include mkdfTransition(fill .5s ease);
                }
            }

            .mkdf-cart-number {
                right: 33px;
                top: 12px;
            }

            &:hover {
                .mkdf-cart-icon {
                    .st1 {
                        fill: #c1ccde;
                    }
                }
            }
        }
    }

    &:hover {

        .mkdf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart{
    svg {
        fill: #fff;
    }

    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }

    &.mkdf-header-cart-svg-path .mkdf-cart-icon .st0 {
        fill: #c9cfff;
    }

    &.mkdf-header-cart-svg-path:hover .mkdf-cart-icon .st0 {
        fill: #fff;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart.mkdf-header-cart-svg-path:hover .mkdf-cart-icon path {
    fill: $header-light-color;
    transition: all .2s ease-in-out;
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart:hover .mkdf-cart-number {
    background-color: #2434b3;
    border-color: #2434b3;
    transition: all .2s ease-in-out;
}

.mkdf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 360px;
    background-color: #fff;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include mkdfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));
    border-radius: 10px;
    box-shadow: 0 5px 20px 0 rgba(23, 44, 82, 0.1);

    ul {
        margin: 30px;
        padding: 0;

        li {
            @include mkdfTableLayout();
            padding: 7px 0;
            box-sizing: border-box;

            &.mkdf-empty-cart {
                border-bottom: 0;
                color: $default-text-color;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .mkdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 70px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();
        vertical-align: top;
        padding-left: 15px;
        color: $default-text-color;

        .mkdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            font-weight: 600;
        }

        .remove {
            position: absolute;
            top: -3px;
            right: 0;
	        font-size: 30px;
	        line-height: 1;
            color: #222;

            &:hover {
                color: $first-main-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .mkdf-quantity {
            display: block;
            margin: 1px 0 0;
            text-transform: uppercase;
            font-size: 13px;
        }

        .amount {
            display: block;
            margin: 0;
            font-size: 11px;
        }
    }

    .mkdf-cart-bottom {
        padding: 13px 0 0;
        margin-top: 23px;

        &:before {
            content: '';
            position: absolute;
            background: $default-border-color;
            height: 1px;
            width: calc(100% + 60px);
            left: -30px;
            top: 0;
        }

        .mkdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 27px;
            font-weight: 500;
            letter-spacing: .01em;
            color: $default-heading-color;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }

            .mkdf-total-amount {
                text-align: right;
            }
        }

        .mkdf-btn-holder {
            text-align: center;
        }

        .mkdf-view-cart {
            @include mkdfRelativeHolderLayout();
            width: auto;
            height: 44px;
            line-height: 40px;
            padding: 0 30px;
            font-size: 13px;
            font-weight: 600;
	        letter-spacing: .03em;
            color: #fff;
            background-color: $first-main-color;
            text-align: center;
            box-sizing: border-box;
            border-radius: 10px;
            border: 2px solid transparent;
            @include mkdfTransition( all .2s ease);

            .mkdf-btn-text {
                padding-right: 31px;
                @include mkdfTransition(padding .17s .08s ease-in-out);
            }

            svg {
                fill: currentColor;
            }

            svg.mkdf-btn-svg-one {
                position: absolute;
                width: 26px;
                top: calc(50% - 4px);
                left: 0;
                opacity: 0;
                @include mkdfTransform(translateX(17px));
                @include mkdfTransition(transform .2s ease-out, opacity .2s ease-out);
            }

            svg.mkdf-btn-svg-two {
                position: absolute;
                right: -5px;
                top: calc(50% - 4px);
                width: 26px;
                opacity: 1;
                @include mkdfTransform(translateX(-33px));
                @include mkdfTransition(transform .2s .25s ease-out, opacity .2s .25s ease-out);
            }

            span {
                display: block;
            }

            &:hover {
                border-color: $default-border-color;
                background-color: transparent;
                color: $default-heading-color;
                @include mkdfTransition(  all .2s ease );

                .mkdf-btn-text {
                    padding-left: 31px;
                    padding-right: 0;
                    @include mkdfTransition(padding .17s .08s ease-in-out);
                }
                svg.mkdf-btn-svg-two {
                    opacity: 0;
                    @include mkdfTransform(translateX(-17px));
                    @include mkdfTransition(transform .2s ease-out, opacity .2s ease-out);
                }
                svg.mkdf-btn-svg-one {
                    opacity: 1;
                    @include mkdfTransform(translateX(25px));
                    @include mkdfTransition(transform .2s .25s ease-out, opacity .2s .25s ease-out);
                }
            }
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */