/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 80px;
    $inner-padding: 15px; // distance between image and content
    $widget-list-item-padding: 15px; // same as sidebar vars

    clear: both;

    li {
        position: relative;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        font-weight: 700;
        font-size: 16px;
        color: #08104d;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 7px;
            margin-top: 7px;

            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 33px;
        }

        .price_slider_wrapper {
            .ui-widget-content {
                background-color: #c8c8c8;
                height: 2px;
                position: relative;

                .ui-slider-handle {
                    background-color: $first-main-color;
                    border-radius: 50%;
                    cursor: pointer;
                    height: 10px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -4px;
                    width: 10px;
                    z-index: 2;

                    &:last-child {
                        @include mkdfTransform(translateX(-50%));
                    }
                }

                .ui-slider-range {
                    background-color: $first-main-color;
                    display: block;
                    height: 2px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 40px 0 0;

            .button {
                float: right;
                padding: 15px 40px 16px;
                @include mkdfButtonSolidHoverColor();
                border-radius: 10px;
                border: 2px solid transparent;
                @include mkdfTransition( all .2s ease);transition: all .2s ease;

                &:hover {
                    border-color: $default-border-color;
                    background-color: transparent;
                    color: $default-heading-color;
                    @include mkdfTransition(  all .2s ease );
                }
            }

            .price_label {
                float: left;
                line-height: 50px; // to match button height
                text-transform: capitalize;
                color: $default-heading-color;
                font-size: 13px;
                font-weight: 600;
                letter-spacing: .02em;
                margin-top: 3px;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products{
        ul {
            li {
                min-height: $thumb;
                position: relative;

                a {
                    img {
                        left: 0;
                        max-width: $thumb;
                        position: absolute;
                        top: $widget-list-item-padding;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 0 0 0 #{$thumb + $inner-padding};
                        position: relative;
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }
            }
        }
    }

    &.widget_products {
        ul li{
            a span {
                padding-top: 25px;
            }
            >ins {
                clear: none;
                margin-left: 5px;
            }
        }
        .star-rating {
            display: none;
        }
    }

    &.widget_product_categories {
        ul {
            margin-top: 12px;

            li {
                overflow: hidden;

                a {
                    letter-spacing: 0.02em;
                    position: relative;
                    font-size: 15px;
                    font-weight: 500;
                    transition: all .2s ease;
                    color: $default-text-color;

                    &:before {
                        content: url('../img/arrow-red.svg');
                        left: -30px;
                        position: absolute;
                        width: 25px;
                        height: 9px;
                        transition: all .2s ease;
                    }

                    &:hover {
                        padding-left: 32px;

                        &:before {
                            left: 0;
                            transition: all .2s ease;
                        }
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include mkdfH5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 5px;
            text-decoration: none;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include mkdfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include mkdfButtonDefaultStyle();
                @include mkdfButtonSize();
                @include mkdfButtonSolidColor();

                &:hover {
                    @include mkdfButtonSolidHoverColor();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */