/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {

    .mkdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: $additional-blue-color;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
            border: none;
        }

        .mkdf-quantity-input {
            position: relative;
            height: 59px;
            width: 59px;
            padding: 0;
            margin: 0 24px 0 0;
            font-family: inherit;
            line-height: 46px;
            outline: 0;
            font-size: 13px;
            border-radius: 10px 0 0 10px;
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            position: absolute;
            right: 0;
            width: 30px;
            height: 30px;
            line-height: 25px;
            cursor: pointer;
            @include mkdfTransition(color .15s ease-in-out);
            font-size: 22px;

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-quantity-plus {
            top: 0;
            border-radius: 0 10px 0 0;
        }

        .mkdf-quantity-minus {
            bottom: 0;
            border-radius: 0 0 10px;
        }
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */